* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Segoe UI, Tahoma, Geneva, Verdana, sans-serif;
}

body {
  color: #333;
  background: #b6b6be;
  padding: 20px;
  line-height: 1.5;
}

.container {
  background: #fff;
  border-radius: 8px;
  max-width: 800px;
  margin: 0 auto;
  padding: 20px 30px;
  box-shadow: 0 2px 8px #0000001a;
}

form {
  margin-bottom: 30px;
}

h2 {
  color: #444;
  margin-bottom: 10px;
}

input[type="text"], select {
  border: 1px solid #ccc;
  border-radius: 4px;
  width: 100%;
  margin: 8px 0 15px;
  padding: 8px 10px;
  font-size: 16px;
}

button {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 5px;
  padding: 10px 16px;
  font-size: 16px;
  transition: background-color .3s;
}

button:hover {
  background-color: #1bb300;
  transform: scale(1.05);
}

.task {
  background-color: #fafafa;
  border: 1px solid #ddd;
  border-radius: 5px;
  margin-bottom: 15px;
  padding: 12px 15px;
}

.task strong {
  font-size: 18px;
}

.task button {
  margin-top: 10px;
}

label {
  font-weight: 600;
}

.filters {
  margin-bottom: 30px;
}

.filters label {
  margin-right: 10px;
}

.filters select {
  min-width: 150px;
  margin-right: 20px;
}

.status-new {
  background-color: #ffd54f;
  border-radius: 50%;
  width: 12px;
  height: 12px;
  display: inline-block;
}

.status-in-progress {
  background-color: #42a5f5;
}

.status-finished {
  background-color: #66bb6a;
}
